/**
 * mirrorxmodal
 */

import { actions } from "mirrorx";
// services
import * as api from "./service";
// 
import { processData, success, Error } from "utils";


export default {
    //  Store 
    name: "inlineEdit",
    //  Model  state
    initialState: {
        cacheData: [],//
        tableData: [],//
        selectData: [],//
        status: 'view',//view=edit=new=del=
        rowEditStatus: true,//
        showLoading: false,
        list: [],
        totalPages: 1,
        total: 0,
        queryParam: {
            pageParams: {
                pageIndex: 0,
                pageSize: 25
            },
            // groupParams: [],
            whereParams: []
        }
    },
    reducers: {
        /**
         *  Redux  Reducer
         * @param {object} state
         * @param {object} data
         */
        updateState(state, data) { //state
            return {
                ...state,
                ...data
            };
        }
    },
    effects: {
        /**
         * 
         * @param {object} param
         */
        async loadList(param, getState) {
            //  Loading , 
            actions.inlineEdit.updateState({
                showLoading: true,
                status: "view",
                rowEditStatus: true,
                selectData: []
            });

            const inlineEditState = getState().inlineEdit;
            const _param = param || inlineEditState.queryParam;
            //  getList 

            let {result} = processData(await api.getList(_param));
            const {data:res}=result;
            const defState = { showLoading: false }
            let _state = null;
            if (res) {
                const { content: list, number, totalPages, totalElements: total } = res;

                const pageIndex = number + 1;
                _state = Object.assign({}, defState, {
                    list,
                    pageIndex,
                    totalPages,
                    total,
                    queryParam: _param,
                    cacheData: list
                })
            }else {
                _state = defState
            }
            actions.inlineEdit.updateState(_state);
        },
        /**
         * 
         *
         * @param {Array} [param=[]] 
         * @returns {bool} 
         */
        async adds(param) {
            actions.inlineEdit.updateState({ showLoading: true });
            let { result } = processData(await api.adds(param),'');
            const {status}=result;
            actions.inlineEdit.updateState({ showLoading: false});
            if (status === 'success') {
                actions.inlineEdit.loadList();
                return true;
            } else {
                return false;
            }
        },
        /**
         * 
         *
         * @param {Array} [param=[]]
         */
        async removes(param, getState) {
            actions.inlineEdit.updateState({ showLoading: true });
            let { result } = processData(await api.removes(param),'');
            const {status}=result;
            actions.inlineEdit.updateState({ showLoading: false });
            if (status === 'success') {
                const inlineEditState = getState().inlineEdit;
                const { queryParam, list, totalPages } = inlineEditState;
                //  getList 

                const { pageParams: { pageIndex } } = queryParam;
                if (pageIndex > 0 && pageIndex + 1 === totalPages && param.length === list.length) {
                    queryParam.pageParams.pageIndex = pageIndex - 1;
                }

                actions.inlineEdit.loadList(queryParam);
                return true;
            } else {
                return false;
            }
        },
        /**
         * 
         *
         * @param {Array} [param=[]]
         */
        async updates(param) {
            actions.inlineEdit.updateState({ showLoading: true });
            let { result } = processData(await api.updates(param),'');
            const {status}=result;
            actions.inlineEdit.updateState({ showLoading: false });
            if (status === 'success') {
                actions.inlineEdit.loadList();
                return true;
            } else {
                return false;
            }
        },
        resetData(status, getState) {
            let cacheData = getState().inlineEdit.cacheData.slice();
            cacheData.forEach(item => {
                delete item._edit;
            });
            let state = {
                list: cacheData
            }
            if (status) {
                state.status = 'view';
            }
            actions.inlineEdit.updateState(state);
        }
    }
};
