/**
 * YearField ()
 */

//React
import React, { Component } from 'react';
//
import PropTypes from 'prop-types';
//
import moment from 'moment';
// https://www.npmjs.com/package/async-validator
import schema from 'async-validator';
import FieldWrap from './FieldWrap'
//
import DatePicker from 'bee-datepicker';
//
import zhCN from "rc-calendar/lib/locale/zh_CN";

//
import './style.less';

//
const { YearPicker } = DatePicker;

//
const propTypes = {
    value: PropTypes.any,
    onChange: PropTypes.func,
    className: PropTypes.string,
    field: PropTypes.string,
    index: PropTypes.number,
    message: PropTypes.string,
    data: PropTypes.array,
    required: PropTypes.bool,
    onValidate: PropTypes.func,
    isFlag: PropTypes.bool,
    validate: PropTypes.bool,
};

//
const defaultProps = {
    field: '',
    index: '',
    message: '',
    data: [],
    required: false,
    isFlag: false,
    validate: false
}

class YearField extends Component {
    /**
     * Creates an instance of YearField.
     * @param {*} props
     * @memberof YearField
     */
    constructor(props) {
        super(props);
        this.state = {
            value: moment(props.value),//
            flag: false,//
            error: false//
        }
    }
    /**
     *  
     *
     * @param {object} nextProps Props
     * @param {object} nextState State
     * @memberof NumberField
     */
    componentWillReceiveProps(nextProps) {
        //
        if (nextProps.validate == true) {
            this.validate();
        }
    }

    /**
     * 
     *
     * @param {string} value
     */
    handlerChange = (value) => {
        let { onChange, field, index, status } = this.props;
        //
        this.setState({ value, flag: status == 'edit' }, () => {
            this.validate();
        });
        //
        onChange && onChange(field, moment(value).format('YYYY'), index);
    }
    /**
     * 
     *
     */
    validate = () => {
        let { required, field, index, onValidate } = this.props;
        let { value } = this.state;
        //
        let descriptor = {
            [field]: { type: "object", required }
        }
        let validator = new schema(descriptor);
        validator.validate({ [field]: value }, (errors, fields) => {
            if (errors) {
                this.setState({
                    error: true
                });
            } else {
                this.setState({
                    error: false
                });
            }
            onValidate && onValidate(field, fields, index);
        });
    }
    render() {
        let { value, error, flag } = this.state;

        let { className, message, required } = this.props;

        return (
            <FieldWrap
                required={required}
                error={error}
                message={message}
                flag={flag}
            >
                <YearPicker
                    className={className}
                    value={value}
                    onChange={this.handlerChange}
                    format={'YYYY'}
                    locale={zhCN}
                    placeholder={""}
                />
            </FieldWrap>
        );
    }
}

YearField.propTypes = propTypes;
YearField.defaultProps = defaultProps;
export default YearField;