//React
import React, { Component } from 'react';
//
import { actions } from "mirrorx";
//Tinper-bee
import { FormControl, Select } from "tinper-bee";
//
import FormList from 'components/FormList';
//
import DatePicker from "tinper-bee/lib/Datepicker";
//
import zhCN from "rc-calendar/lib/locale/zh_CN";

//
import { deepClone } from "utils";
//
import { RefIuapDept } from 'components/RefViews';
//
import SearchPanel from 'components/SearchPanel';
import SelectMonth from 'components/SelectMonth';
import Alert from 'components/Alert';



//
const FormItem = FormList.Item;
const { Option } = Select;
const format = "YYYY";
const { YearPicker } = DatePicker;

class SearchAreaForm extends Component {

    constructor(props) {
        super(props);
        this.state = {
            show: false
        }
    }

    /** 
     * @param {array} error 
     * @param {json} values 
     */
    search = () => {
        let { status, form: {validateFields} } = this.props;

        validateFields((err, values) => {
            //
            if (values.year) {
                values.year = values.year.format('YYYY');
            }
            if (values.dept) {
                values.dept = JSON.parse(values.dept).refpk;
            }
            //
            if (status != 'view') {
                this.setState({
                    show: true,
                    values
                });
            } else {
                this.getQuery(values, 0);
            }
        })

    }

    /**
     *  
     *
     */
    reset = () => {
        this.props.form.resetFields();
    }

    /**
     * 
     *
     */
    onClickGo = () => {
        this.getQuery(this.state.values, 0);
        this.setState({ show: false });
        actions.inlineEdit.updateState({ status: 'view', rowEditStatus: true });
    }

    /**
     * 
     *
     */
    onClickCancel = () => {
        this.setState({ show: false });
    }

    /**
     *   type01
     *
     * @param {array} values 
     * @param {number} type 
     */
    getQuery = (values, type) => {
        let queryParam = deepClone(this.props.queryParam);
        let { pageParams, whereParams } = queryParam;

        whereParams = deepClone(whereParams);
        pageParams.pageIndex = 0;
        for (let key in values) {
            for (const [index, elem] of whereParams.entries()) {
                if (key === elem.key) {
                    whereParams.splice(index, 1);
                    break;
                }
            }
            if ((values[key] || values[key] === 0) && type === 0) {
                let condition = "LIKE";
                // 
                const equalArray = ["code", "month"];
                const greaterThanArray = ["serviceYearsCompany"];
                if (equalArray.includes(key)) { // 
                    condition = "EQ";
                }
                if (greaterThanArray.includes(key)) { // 
                    condition = "GTEQ";
                }
                whereParams.push({ key, value: values[key], condition }); //
            }
        }

        queryParam.whereParams = whereParams;
        if (type === 0) { // 
            actions.inlineEdit.loadList(queryParam);
        }
        // actions.inlineEdit.updateState(queryParam);

    }


    render() {
        const { form: { getFieldProps }, searchOpen, onCallback } = this.props;
        return (
            <SearchPanel
                searchOpen={searchOpen}
                reset={this.reset}
                onCallback={onCallback}
                search={this.search}>

                <FormList size="sm">
                    <FormItem
                        label=""
                    >
                        <FormControl placeholder="" {...getFieldProps('code', { initialValue: '' })} />
                    </FormItem>

                    <FormItem
                        label=""
                    >
                        <FormControl placeholder="" {...getFieldProps('name', { initialValue: '' })} />
                    </FormItem>

                    <FormItem
                        label=""
                    >
                        <RefIuapDept {...getFieldProps('dept', { initialValue: '' })} />
                    </FormItem>

                    <FormItem
                        label=""
                    >
                        <YearPicker
                            {...getFieldProps('year', { initialValue: null })}
                            format={format}
                            locale={zhCN}
                            placeholder=""
                        />
                    </FormItem>

                    <FormItem
                        label=""
                    >
                        <SelectMonth  {...getFieldProps('month', { initialValue: '' })} />
                    </FormItem>

                    <FormItem
                        label=""
                    >
                        <Select {...getFieldProps('exdeeds', { initialValue: '' })}>
                            <Option value=""></Option>
                            <Option value="0"></Option>
                            <Option value="1"></Option>
                        </Select>
                    </FormItem>
                </FormList>

                <Alert show={this.state.show} context=" ?" confirmFn={this.onClickGo} cancelFn={this.onClickCancel} />
            </SearchPanel>
        )
    }
}

export default FormList.createForm()(SearchAreaForm)
